.administerQuiz,
.editQuiz {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 100%;
}
.editQuiz {
  top: 28px;
}
.administerQuizParent {
  position: absolute;
  top: 18px;
  left: 15px;
  width: 96px;
  height: 40px;
}
.moreViewModal {
  position: relative;
  border-radius: 10px;
  background-color: #fff;
  border: 1px solid #eaecee;
  box-sizing: border-box;
  width: 127px;
  height: 77px;
  overflow: hidden;
  max-width: 100%;
  max-height: 100%;
  text-align: left;
  font-size: 12px;
  color: #000;
  font-family: Montserrat;
}
