.multipleChoice,
.singleChoice {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 100%;
}
.multipleChoice {
  top: 28px;
}
.singleChoiceParent {
  position: absolute;
  top: 3px;
  left: 27px;
  width: 93px;
  height: 40px;
}
.checkBoxIcon,
.radioButtonCheckedIcon {
  position: absolute;
  top: 0;
  left: 0;
  width: 19px;
  height: 19px;
  overflow: hidden;
}
.checkBoxIcon {
  top: 26px;
}
.groupParent {
  position: absolute;
  top: 16px;
  left: 11px;
  width: 120px;
  height: 45px;
}
.addQuestionModal {
  position: relative;
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid #eaecee;
  box-sizing: border-box;
  width: 148px;
  height: 83px;
  overflow: hidden;
  max-width: 100%;
  max-height: 100%;
  text-align: left;
  font-size: 12px;
  color: #000;
  font-family: Montserrat;
}
